.faq-pageWrapper {
    overflow: hidden;
    font-family: "NewNarravite";
}

.faq-container {
    z-index: 1;
    display: grid;
    position: relative;
    grid-area: footer;
    min-height: calc(100vh - 137.88px);
    // background-color: #e562a0;
    background-color: #e562a0;

    .bubbles {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        height: 1rem;
        background: #e562a0;
        filter: url("#blob");
        .bubble {
            position: absolute;
            left: var(--position, 50%);
            background: #e562a0;
            border-radius: 100%;
            animation: bubble-size var(--time, 4s) ease-in infinite
                    var(--delay, 0s),
                bubble-move var(--time, 4s) ease-in infinite var(--delay, 0s);
            transform: translate(-50%, 100%);
        }
    }

    .content {
        z-index: 2;
        display: grid;
        grid-template-columns: 1fr auto;
        grid-gap: 4rem;
        padding: 2rem;
        background: #e562a0;
        a,
        p {
            color: #f5f7fa;
            text-decoration: none;
        }
        b {
            color: white;
        }
        p {
            margin: 0;
            font-size: 0.75rem;
        }
        > div {
            display: flex;
            flex-direction: column;
            justify-content: center;
            > div {
                margin: 0.25rem 0;
                > * {
                    margin-right: 0.5rem;
                }
            }
        }
    }
}

@keyframes bubble-size {
    0% {
    }
    75% {
        width: var(--size, 4rem);
        height: var(--size, 4rem);
    }
    100% {
        width: 0rem;
        height: 0rem;
    }
}
@keyframes bubble-move {
    0% {
        bottom: -4rem;
    }
    100% {
        bottom: var(--distance, 10rem);
    }
}
